<template>
  <div class="home">
    <Banner :movie="featuredMovie" />
    <MovieRow title="Popular Movie" :fetchUrl="popularMoviesUrl" />
    <MovieRow title="Latest Movie" :fetchUrl="newReleasesUrl" />
    <MovieRow title="Action Movie" :fetchUrl="actionMoviesUrl" />
  </div>
</template>

<script>
import { onMounted, onBeforeUnmount, ref } from 'vue';
import Banner from '@/views/home-main/MovieBanner.vue';
import MovieRow from '@/views/home-main/MovieRow.vue';
import URLService from '@/util/movie/URL';

export default {
  name: 'HomeMain',
  components: {
    Banner,
    MovieRow,
  },
  setup() {
    // API ÌÇ§ Í∞ÄÏ†∏Ïò§Í∏∞
    const apiKey = localStorage.getItem('TMDb-Key') || '';

    // Î∞òÏùëÌòï Îç∞Ïù¥ÌÑ∞ Ï†ïÏùò
    const featuredMovie = ref(null);
    const popularMoviesUrl = ref(URLService.getURL4PopularMovies(apiKey));
    const newReleasesUrl = ref(URLService.getURL4ReleaseMovies(apiKey));
    const actionMoviesUrl = ref(URLService.getURL4GenreMovies(apiKey, '28'));

    // ÎåÄÌëú ÏòÅÌôî Î∂àÎü¨Ïò§Í∏∞ Ìï®Ïàò
    const loadFeaturedMovie = async () => {
      try {
        featuredMovie.value = await URLService.fetchFeaturedMovie(apiKey);
      } catch (error) {
        console.error("Error fetching featured movie:", error);
      }
    };

    // Ïä§ÌÅ¨Î°§ Î¶¨Ïä§ÎÑà Ï¥àÍ∏∞Ìôî Ìï®Ïàò
    const scrollListener = () => {
      const header = document.querySelector('.app-header');
      if (window.scrollY > 50) {
        header?.classList.add('scrolled');
      } else {
        header?.classList.remove('scrolled');
      }
    };

    // Ïª¥Ìè¨ÎÑåÌä∏ ÎùºÏù¥ÌîÑÏÇ¨Ïù¥ÌÅ¥: ÎßàÏö¥Ìä∏ Ïãú Ïä§ÌÅ¨Î°§ Î¶¨Ïä§ÎÑà Îì±Î°ù
    onMounted(() => {
      loadFeaturedMovie();
      window.addEventListener('scroll', scrollListener);
    });

    // Ïª¥Ìè¨ÎÑåÌä∏ ÎùºÏù¥ÌîÑÏÇ¨Ïù¥ÌÅ¥: Ïñ∏ÎßàÏö¥Ìä∏ Ïãú Ïä§ÌÅ¨Î°§ Î¶¨Ïä§ÎÑà Ìï¥Ï†ú
    onBeforeUnmount(() => {
      window.removeEventListener('scroll', scrollListener);
    });

    return {
      featuredMovie,
      popularMoviesUrl,
      newReleasesUrl,
      actionMoviesUrl,
    };
  },
};
</script>

<style scoped>
/* Ìôà Ïª¥Ìè¨ÎÑåÌä∏ Ïä§ÌÉÄÏùº */
.home {
  display: block; /* :host ÎåÄÏ≤¥ */
}

/* Ïä§ÌÅ¨Î°§Î∞î ÏÑ§Ï†ï */
html,
body {
  overflow-y: scroll;
}

body {
  background-color: #141414;
}
</style>
