import { ref, computed } from 'vue';

const wishlist = ref([]); // VueÏùò Î∞òÏùëÏÑ±ÏùÑ Ïú†ÏßÄÌïòÍ∏∞ ÏúÑÌïú ref ÏÑ†Ïñ∏

export function useWishlist() {
  // Î°úÏª¨Ïä§ÌÜ†Î¶¨ÏßÄÏóêÏÑú ÏúÑÏãúÎ¶¨Ïä§Ìä∏ Î∂àÎü¨Ïò§Í∏∞
  const loadWishlist = () => {
    try {
      const storedWishlist = localStorage.getItem('movieWishlist');
      if (storedWishlist) {
        wishlist.value = JSON.parse(storedWishlist);
      } else {
        wishlist.value = []; // Î°úÏª¨Ïä§ÌÜ†Î¶¨ÏßÄÏóê ÏóÜÏúºÎ©¥ Îπà Î∞∞Ïó¥Î°ú Ï¥àÍ∏∞Ìôî
      }
    } catch (error) {
      console.error('Failed to load wishlist from localStorage:', error);
      wishlist.value = []; // Ïò§Î•ò Î∞úÏÉù Ïãú Îπà Î∞∞Ïó¥Î°ú Ï¥àÍ∏∞Ìôî
    }
  };

  // Î°úÏª¨Ïä§ÌÜ†Î¶¨ÏßÄÏóê ÏúÑÏãúÎ¶¨Ïä§Ìä∏ Ï†ÄÏû•
  const saveWishlist = () => {
    try {
      localStorage.setItem('movieWishlist', JSON.stringify(wishlist.value));
    } catch (error) {
      console.error('Failed to save wishlist to localStorage:', error);
    }
  };

  // ÏúÑÏãúÎ¶¨Ïä§Ìä∏Ïóê ÏòÅÌôî Ï∂îÍ∞Ä/Ï†úÍ±∞
  const toggleWishlist = (movie) => {
    const index = wishlist.value.findIndex((item) => item.id === movie.id);
    if (index === -1) {
      wishlist.value = [...wishlist.value, movie]; // ÏÉàÎ°úÏö¥ Î∞∞Ïó¥Î°ú Ïû¨Ìï†Îãπ (Î∞òÏùëÏÑ± Î≥¥Ïû•)
    } else {
      wishlist.value = wishlist.value.filter((item) => item.id !== movie.id); // ÏÉàÎ°úÏö¥ Î∞∞Ïó¥Î°ú Ïû¨Ìï†Îãπ
    }
    saveWishlist();
  };

  // ÏòÅÌôîÍ∞Ä ÏúÑÏãúÎ¶¨Ïä§Ìä∏Ïóê ÏûàÎäîÏßÄ ÌôïÏù∏
  const isInWishlist = (movieId) => {
    return wishlist.value.some((item) => item.id === movieId);
  };

  return {
    wishlist,
    toggleWishlist,
    isInWishlist,
    loadWishlist,
  };
}
