import { createRouter, createWebHashHistory } from 'vue-router';
import { useAuthGuard } from '@/guards/authGuard'; // Ïù∏Ï¶ù Í∞ÄÎìú Ï∂îÍ∞Ä

// Ïª¥Ìè¨ÎÑåÌä∏ Î∂àÎü¨Ïò§Í∏∞
import SignIn from '@/components/sign-in/Sign-In.vue';
import HomeMain from '@/components/home/main/HomeMain.vue';
import HomeWishlist from '@/components/home/wishlist/HomeWishlist.vue';
import HomeSearch from '@/components/search/HomeSearch.vue';
import HomePopular from '@/components/home/popular/HomePopular.vue';
import MovieDetail from '@/views/MovieDetail.vue';

const routes = [
  {
    path: '/',
    component: () => import('@/App.vue'),
    children: [
      {
        path: '',
        component: HomeMain,
        meta: { requiresAuth: true }, // Ïù∏Ï¶ùÏù¥ ÌïÑÏöîÌïú ÎùºÏö∞Ìä∏
      },
      {
        path: 'popular',
        component: HomePopular,
        meta: { requiresAuth: true },
      },
      {
        path: 'wishlist',
        component: HomeWishlist,
        meta: { requiresAuth: true },
      },
      {
        path: 'search',
        component: HomeSearch,
        meta: { requiresAuth: true },
      },
    ],
  },
  {
    path: '/signin',
    component: SignIn,
  },
  {
    path: '/movie/:movieId',
    name: 'MovieDetail',
    component: MovieDetail,
    props: true,
    meta: { requiresAuth: true },
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
  linkExactActiveClass: 'exact-active-link', // Ï†ïÌôïÌûà ÏùºÏπòÌïòÎäî Í≤ΩÏö∞ Ï†ÅÏö©Ìï† ÌÅ¥ÎûòÏä§
});

// Ïù∏Ï¶ù Í∞ÄÎìú Ï†ÅÏö©
useAuthGuard(router);

export default router;
