<!-- @/components/home/popular/HomePopular.vue -->
<template>
  <div class="popular-container">
    <!-- Î∑∞ Ï†ÑÌôò Î≤ÑÌäº -->
    <div class="view-toggle">
      <button @click="setView('grid')" :class="{ active: currentView === 'grid' }">
        <font-awesome-icon :icon="faTh" />
      </button>
      <button @click="setView('list')" :class="{ active: currentView === 'list' }">
        <font-awesome-icon :icon="faBars" />
      </button>
    </div>

    <!-- Í∑∏Î¶¨Îìú Î∑∞ -->
    <MovieGrid v-if="currentView === 'grid'" title="Ïù∏Í∏∞ ÏòÅÌôî" :fetchUrl="fetchURL" />

    <!-- Î¶¨Ïä§Ìä∏ Î∑∞: Î¨¥Ìïú Ïä§ÌÅ¨Î°§ -->
    <MovieInfiniteScroll
      v-if="currentView === 'list'"
      :apiKey="apiKey"
      :genreCode="genreCode"
      :sortingOrder="sortingOrder"
      :voteAverage="voteAverage"
    />
  </div>
</template>

<script>
import { ref, onMounted, onUnmounted } from 'vue';
import { faTh, faBars } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';
import MovieGrid from '@/views/views/MovieGrid.vue';
import MovieInfiniteScroll from '@/views/views/MovieInfiniteScroll.vue';
import urlService from '@/util/movie/URL'; // Ïã±Í∏ÄÌÜ§ Ïù∏Ïä§ÌÑ¥Ïä§Î•º Î∞îÎ°ú ÏÇ¨Ïö©

export default {
  name: 'HomePopular',
  components: {
    FontAwesomeIcon,
    MovieGrid,
    MovieInfiniteScroll,
  },
  setup() {
    const apiKey = localStorage.getItem('TMDb-Key') || '';
    const currentView = ref('grid');
    const originalOverflow = ref(document.body.style.overflow);

    // Î¨¥Ìïú Ïä§ÌÅ¨Î°§ ÌïÑÌÑ∞ÎßÅ Îç∞Ïù¥ÌÑ∞
    const genreCode = ref('28'); // Í∏∞Î≥∏ Ïû•Î•¥: Ïï°ÏÖò
    const sortingOrder = ref('all'); // Í∏∞Î≥∏ Ïñ∏Ïñ¥: Ï†ÑÏ≤¥
    const voteAverage = ref(-1); // Í∏∞Î≥∏ ÌèâÏ†ê: Ï†ÑÏ≤¥

    // Î∑∞ Ï†ÑÌôò Ìï®Ïàò
    const setView = (view) => {
      currentView.value = view;
      if (view === 'grid') {
        disableScroll();
      } else {
        enableScroll();
      }
    };

    // Ïä§ÌÅ¨Î°§ Ï†úÏñ¥
    const disableScroll = () => {
      document.body.style.overflow = 'hidden';
    };

    const enableScroll = () => {
      document.body.style.overflow = originalOverflow.value || 'auto';
    };

    const fetchURL = urlService.getURL4PopularMovies(apiKey);

    onMounted(() => {
      if (currentView.value === 'grid') disableScroll();
    });

    onUnmounted(() => {
      enableScroll(); // Ïª¥Ìè¨ÎÑåÌä∏ Ïñ∏ÎßàÏö¥Ìä∏ Ïãú Ïä§ÌÅ¨Î°§ Î≥µÍµ¨
    });

    return {
      faTh,
      faBars,
      apiKey,
      currentView,
      setView,
      fetchURL,
      genreCode,
      sortingOrder,
      voteAverage,
    };
  },
};
</script>

<style scoped>
.popular-container {
  padding: 20px;
}

.view-toggle {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 20px;
}

.view-toggle button {
  background-color: #333;
  color: white;
  border: none;
  padding: 10px 15px;
  margin-left: 10px;
  cursor: pointer;
  border-radius: 4px;
}

.view-toggle button.active {
  background-color: #535bf2;
}

@media (max-width: 768px) {
  .view-toggle {
    margin-bottom: 30px;
  }
}
</style>
